import { FunctionComponent } from 'preact';
import { HTMLAttributes } from 'preact/compat';
import { Icon, InLineAlert } from '@adobe-commerce/elsie/components';
import { useText } from '@adobe-commerce/elsie/i18n';
import { Cart, HeartFilled, Trash, Warning } from '@adobe-commerce/elsie/icons';

export interface WishlistAlertProps extends HTMLAttributes<HTMLDivElement> {
  action: 'add' | 'remove' | 'move' | 'addError' | 'removeError';
  item?: { product: { name: string } };
  routeToWishlist?: string;
}

export const WishlistAlert: FunctionComponent<WishlistAlertProps> = ({
  action,
  item,
  routeToWishlist,
}) => {
  const dictionary = useText({
    addHeading: 'Wishlist.Alert.addProduct.heading',
    addMessage: 'Wishlist.Alert.addProduct.message',
    removeHeading: 'Wishlist.Alert.removeProduct.heading',
    removeMessage: 'Wishlist.Alert.removeProduct.message',
    moveHeading: 'Wishlist.Alert.moveToCart.heading',
    moveMessage: 'Wishlist.Alert.moveToCart.message',
    viewWishlist: 'Wishlist.Alert.viewWishlist',
    addErrorHeading: 'Wishlist.Alert.addError.heading',
    addErrorMessage: 'Wishlist.Alert.addError.message',
    removeErrorHeading: 'Wishlist.Alert.removeError.heading',
    removeErrorMessage: 'Wishlist.Alert.removeError.message',
  });

  if (!action) {
    return null;
  }

  const heading = dictionary[`${action}Heading`];
  const message = dictionary[`${action}Message`];
  const iconMap = {
    add: HeartFilled,
    remove: Trash,
    move: Cart,
    addError: Warning,
    removeError: Warning,
  };

  const isWishlistPage = routeToWishlist
    ? location.href.includes(routeToWishlist)
    : false;

  return (
    <InLineAlert
      data-testid="wishlist-alert"
      heading={heading}
      description={
        item ? message.replace('{product}', item.product.name) : message
      }
      type={
        action === 'addError' || action === 'removeError'
          ? 'warning'
          : 'success'
      }
      icon={<Icon source={iconMap[action]} size="16" />}
      actionButtonPosition="top"
      additionalActions={
        !isWishlistPage && routeToWishlist
          ? [
              {
                label: dictionary.viewWishlist,
                onClick: () => {
                  location.href = routeToWishlist;
                },
              },
            ]
          : undefined
      }
    />
  );
};
